import VueRouter from 'vue-router'

import AppSpout from './components/AppSpout'

function getProps (route) {
  let targetPath = []
  for (let i = 0; i < 16; i++) {
    targetPath.push(route.params[`targetPath${i}`])
  }
  targetPath = targetPath.filter((e) => { return e !== undefined })
  return {
    targetAccount: route.params.targetAccount,
    targetRepository: route.params.targetRepository,
    targetPath
  }
}

const routerConfig = {
  routes: []
}
for (let i = 0; i <= 16; i++) {
  let routeConfig = {
    path: '/:targetAccount/:targetRepository/',
    pathToRegexpOptions: { strict: i=0 },
    component: AppSpout,
    props: getProps
  }
  for (let j = 1; j <= i; j++) {
    routeConfig.path += `:targetPath${j}/`
  }

  routerConfig.routes.push(Object.assign({}, routeConfig))
}

let router = null

function MakeRouter () {
  return router || (router = new VueRouter(routerConfig))
}

export default MakeRouter
