import Vuex from 'vuex'
import _ from 'underscore'

const mutations = {
  setGitData (state, newGitData) {
    console.log('setGitData', {newGitData})
    state.gitData = newGitData
  },
  setGitContents (state, newGitContents) {
    console.log('setGitContents', {newGitContents})
    state.gitContents = newGitContents
  },
  setTargetAccount (state, newTargetAccount) {
    console.log('setting target account', newTargetAccount)
    state.targetAccount = newTargetAccount
  },
  setTargetRepository (state, newTargetRepository) {
    console.log('setting target repository', newTargetRepository)
    state.targetRepository = newTargetRepository
  },
  setTargetPath (state, newTargetPath) {
    console.log('setting target path', newTargetPath)
    state.targetPath = newTargetPath
  }
}

const getters = {
  getDirectories () {

  },
  getBreadcrumbs (state) {
    let fullPath = `#/${state.targetAccount}/${state.targetRepository}`
    return [{
      text: state.targetRepository,
      href: `${fullPath}/`
    }].concat(_.map(state.targetPath, (path) => {
      fullPath += `/${path}`
      return {
        text: path,
        href: fullPath
      }
    }))
  },
  getFormattedContents (state) {
    let contents = state.gitContents
    let suffix = state.targetPath.split('.').pop().split('/').shift()
    if (suffix !== 'md') {
      contents = '```' + contents + '```'
    }
    return contents
  },
  getApiUrl (state) {
    let value = `https://api.github.com/repos/${state.targetAccount}/${state.targetRepository}/contents/${state.targetPath.join('/')}`
    console.debug(value)
    return value
  },
  getParentApiUrl (state) {
    let value = `https://api.github.com/repos/${state.targetAccount}/${state.targetRepository}/contents/${state.targetPath.slice(0, -1).join('/')}`
    console.debug(value)
    return value
  },
  getContentUrl (state) {
    let value = `https://raw.githubusercontent.com/${state.targetAccount}/${state.targetRepository}/master/${state.targetPath.join('/')}`
    console.debug(value)
    return value
  },
  isLoading (state) {
    return state.gitData === null
  }
}

const actions = {
  async update ({state, getters, commit, dispatch}, {targetAccount, targetRepository, targetPath}) {
    console.debug('update')
    let hasChanged = false
    if (!state.gitContents) {
      console.debug('!state.gitContents')
      hasChanged = true
    }

    if (targetAccount && targetAccount !== state.targetAccount) {
      console.debug('targetAccount !== state.targetAccount')
      hasChanged = true
    }

    if (targetRepository && targetRepository !== state.targetRepository) {
      console.debug('targetRepository !== state.targetRepository')
      hasChanged = true
    }

    if (!hasChanged && targetPath) {
      console.log({targetPath, stateTargetPath: state.targetPath})
      const lengthCheck = targetPath.length === state.targetPath.length
      let comparisonCheck = null

      if (lengthCheck) {
        // start true, prove wrong
        comparisonCheck = true
        for (let targetPathIndex in targetPath) {
          if (targetPath[targetPathIndex] !== state.targetPath[targetPathIndex]) {
            comparisonCheck = false
            break
          }
        }
      }

      console.log({lengthCheck, comparisonCheck})
      if (!lengthCheck || !comparisonCheck) {
        hasChanged = true
      }
    }

    commit('setTargetAccount', targetAccount)
    commit('setTargetRepository', targetRepository)
    commit('setTargetPath', targetPath)

    if (hasChanged) {
      console.debug('hasChanged')
      dispatch('fetchGitData')
      dispatch('fetchGitContents')
    }
  },
  async fetchGitData ({getters, commit, dispatch}) {
    console.debug('fetchGitData')
    commit('setGitData', {})
    commit('setGitData', await new Promise((resolve) => {
      var xmlHttp = new XMLHttpRequest()
      xmlHttp.onreadystatechange = () => {
        if (xmlHttp.readyState === 4 && xmlHttp.status === 200) {
          let result = JSON.parse(xmlHttp.responseText)
          if (!Array.isArray(result)) {
            result = null
          }
          resolve(result)
        }
      }
      xmlHttp.open('GET', getters.getApiUrl, true)
      xmlHttp.send(null)
    }) || await new Promise((resolve) => {
      var xmlHttp = new XMLHttpRequest()
      xmlHttp.onreadystatechange = () => {
        if (xmlHttp.readyState === 4 && xmlHttp.status === 200) {
          let result = JSON.parse(xmlHttp.responseText)
          if (!Array.isArray(result)) {
            result = null
          }
          resolve(result)
        }
      }
      xmlHttp.open('GET', getters.getParentApiUrl, true)
      xmlHttp.send(null)
    }))
  },
  async fetchGitContents ({getters, commit}) {
    console.debug('fetchGitContents')
    commit('setGitContents', 'Loading')
    commit('setGitContents', await new Promise((resolve) => {
      var xmlHttp = new XMLHttpRequest()
      xmlHttp.onreadystatechange = () => {
        if (xmlHttp.readyState === 4 && xmlHttp.status === 200) {
          resolve(xmlHttp.responseText)
        }
      }
      xmlHttp.open('GET', getters.getContentUrl, true)
      xmlHttp.send(null)
    }))
  }
}

module.exports = exports = function MakeStore () {
  const state = {
    targetAccount: '',
    targetRepository: '',
    targetPath: [],
    gitData: {},
    gitContents: 'Loading'
  }

  const store = new Vuex.Store({
    state,
    mutations,
    getters,
    actions
  })

  console.log(store.state.targetRepository)

  return store
}
