// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'babel-polyfill'

import Vue from 'vue'

import Vuex from 'vuex'
import VueRouter from 'vue-router'

import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

import App from './App'

import MakeRouter from './MakeRouter'
import MakeStore from './MakeStore'

Vue.config.productionTip = false

Vue.use(BootstrapVue)
Vue.use(Vuex)
Vue.use(VueRouter)

const el = '#app'
const render = h => h(App)
const store = MakeStore()
const router = MakeRouter()

router.afterEach((to) => {
  const targetAccount = to.params.targetAccount
  const targetRepository = to.params.targetRepository
  const targetPath = [
    to.params.targetPath1,
    to.params.targetPath2,
    to.params.targetPath3,
    to.params.targetPath4,
    to.params.targetPath5,
    to.params.targetPath6,
    to.params.targetPath7,
    to.params.targetPath8
  ].filter((e) => { return e !== undefined })
  console.log([
    to.params.targetPath1,
    to.params.targetPath2,
    to.params.targetPath3,
    to.params.targetPath4,
    to.params.targetPath5,
    to.params.targetPath6,
    to.params.targetPath7,
    to.params.targetPath8
  ])
  store.dispatch('update', {targetAccount, targetRepository, targetPath})
})

/* eslint-disable no-new */
new Vue({el, render, router, store})
